import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ShippingCalculationModel } from 'src/app/models/generated/ShippingCalculationModel';
import { ShippingService } from 'src/app/services/shipping.service';

@Component({
  selector: 'app-order-summary-content-before-widget',
  templateUrl: './order-summary-content-before-widget.component.html',
  styleUrls: ['./order-summary-content-before-widget.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class OrderSummaryContentBeforeWidgetComponent implements OnInit {
  loading: boolean = true;
  shippingCalculation: ShippingCalculationModel;

  constructor(private readonly _shippingService: ShippingService) { }

  ngOnInit() {
    this.loading = true;
    this._shippingService.getShippingCalculation().subscribe(data => {
      this.shippingCalculation = data;
      this.loading = false;
    });
  }
}
