import { Pipe, PipeTransform } from '@angular/core';
import { CommonService } from '../services/common.service';
import { ResourceKeys } from '../constants/generated/ResourceKeys';

@Pipe({
    name: 'localizer'
})
export class LocalizerPipe implements PipeTransform {
    constructor(private _commonService: CommonService) { }

    transform(resourceKey: string) {
        const promise = this._commonService.getPluginAllLocaleStringResourcesOfCache().toPromise();
        return promise.then((resources: { [key: string]: string }) => {            
            if (ResourceKeys[resourceKey]) {
                const resourceName = ResourceKeys[resourceKey];
                if (resources[resourceName]) {
                    return resources[resourceName];
                } else {
                    return resourceName;
                }
            }else{
                return resourceKey;
            }
        });
    }
}