import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ShippingService } from 'src/app/services/shipping.service';
import { forkJoin } from 'rxjs';
import { CityModel } from 'src/app/models/generated/CityModel';
import { FormGroup } from '@angular/forms';
import { ShippingMethodModel } from 'src/app/models/generated/ShippingMethodModel';

@Component({
  selector: 'app-shipping-method-select',
  templateUrl: './shipping-method-select.component.html',
  styleUrls: ['./shipping-method-select.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class ShippingMethodSelectComponent implements OnInit {
  shippingMethodList: ShippingMethodModel[];
  loading: boolean = false;
  cities: CityModel[];  
  shippingMethodValid: any = {};
  blockedUI: boolean = false;

  constructor(private readonly _shippingService: ShippingService) {
  }

  ngOnInit() {
    this.loadData();
  }

  ngAfterViewInit() {              
      document.addEventListener('shippingMethodNextStepButtonClickEvent', this.saveData.bind(this));
      document.addEventListener('resetLoadWaitingEvent', this.resetLoadWaitingEvent.bind(this));
  }

  loadData() {
    this.loading = true;
    forkJoin(
      this._shippingService.getShippingMethods(),
      this._shippingService.getCitiesOfSelectedShippingComputation()
    )
      .subscribe(([shippingMethodList, cities]) => {
        this.shippingMethodList = shippingMethodList;
        this.cities = cities;

        this.loading = false;
      });
  }

  saveData(event: CustomEvent) {    
    let selectedShippingMethod = this.shippingMethodList.filter((item) => {
      return item.isSelected;
    })[0];
    
    if (!this.shippingMethodValid[selectedShippingMethod.displayOrder]) {             
      alert("Please fill in the required fields.");      
      event.preventDefault();
      return;
    }

    this.blockedUI = true;

    this._shippingService.updateInfo(selectedShippingMethod).subscribe(() => {
      event.detail.shippingMethod.saveCallback();
    });
  }

  onTabOpen(e: any) {
    this.shippingMethodList.forEach(element => { element.isSelected = false; });
    this.shippingMethodList[e.index].isSelected = true;
  }

  checkModel(e: FormGroup, shippingMethod: ShippingMethodModel) {        
    this.shippingMethodValid[shippingMethod.displayOrder] = e.valid;    
  }

  resetLoadWaitingEvent(event: CustomEvent){    
    this.blockedUI = false;
    event.detail.shippingMethod.resetLoadWaitingCallback();
  }
}
