import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation} from '@angular/core';
import { ShippingService } from 'src/app/services/shipping.service';
import { PickupPointsFilterModel } from 'src/app/models/generated/PickupPointsFilterModel';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ShippingMethodModel } from 'src/app/models/generated/ShippingMethodModel';
import { ShippingFormFieldNames } from 'src/app/constants/generated/ShippingFormFieldNames';
import { DictionaryModel } from 'src/app/models/generated/DictionaryModel';
import { AttributeControlTypeEnum } from 'src/app/constants/generated/AttributeControlTypeEnum';

@Component({
  selector: 'app-shipping-form',
  templateUrl: './shipping-form.component.html',
  styleUrls: ['./shipping-form.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class ShippingFormComponent implements OnInit {
  @Input() get shippingMethod() {
    return this._shippingMethod;
  }
  set shippingMethod(value: ShippingMethodModel) {
    this._shippingMethod = value;
  }
  @Output() shippingMethodChange = new EventEmitter<ShippingMethodModel>();

  @Output() shippingFormGroup = new EventEmitter<FormGroup>();

  @Input() cities: DictionaryModel[];
  @Input() shippingCarrierTab: string;

  pickupPoints: DictionaryModel[];
  loadingPickupPoints: boolean = false;
  shippingForm: FormGroup;

  selectedCity: DictionaryModel;
  selectedPickupPoint: DictionaryModel;

  get shippingFormFieldNames() {
    return ShippingFormFieldNames;
  }

  get attributeControlTypeEnum() {
    return AttributeControlTypeEnum;
  }

  private _shippingMethod: ShippingMethodModel;

  constructor(private readonly _shippingService: ShippingService,
    private readonly _fb: FormBuilder) { }

  ngOnInit() {
    this.selectedCity = this.findSelectedCity();
    if (this.selectedCity) {
      this.loadPickupPoints(true);
    }

    var controlsConfig: { [key: string]: any; } = {};
    
    // FirstNameTextBox
    let required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.FirstNameTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.FirstNameTextBox] = new FormControl(this._shippingMethod.firstName, required);

    // LastNameTextBox
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.LastNameTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.LastNameTextBox] = new FormControl(this._shippingMethod.lastName, required);

    // PhoneNumberTextBox    
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.PhoneNumberTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.PhoneNumberTextBox] = new FormControl(this._shippingMethod.phoneNumber, required);

    // ZipPostalCodeTextBox
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.ZipPostalCodeTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.ZipPostalCodeTextBox] = new FormControl(this._shippingMethod.zipPostalCode, required);

    // CityTextBox
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.CityTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.CityTextBox] = new FormControl(this._shippingMethod.city, required);

    // AddressTextBox
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.AddressTextBox) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.AddressTextBox] = new FormControl(this._shippingMethod.address, required);

    // CityDropdownList
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.CityDropdownList) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.CityDropdownList] = new FormControl(this.selectedCity, required);

    // AddressDropdownList
    required = this._shippingMethod.hiddenShippingFields.indexOf(ShippingFormFieldNames.AddressDropdownList) == -1
      ? Validators.required : null;
    controlsConfig[ShippingFormFieldNames.AddressDropdownList] = new FormControl(this.selectedPickupPoint, required);

    // AdditionalShippingFields
    this._shippingMethod.additionalShippingFields.forEach(element => {
      controlsConfig[element.nameResourceKey] = new FormControl(element.value, Validators.required);
    });

    this.shippingForm = this._fb.group(controlsConfig);

    setTimeout(() => {
      this.shippingForm.controls[ShippingFormFieldNames.PhoneNumberTextBox].setValue(this._shippingMethod.phoneNumber);
    }, 0);
    
    this.shippingFormGroup.emit(this.shippingForm);
  }

  findSelectedCity(): DictionaryModel {
    let citySelected = this._shippingMethod.city;
    if (citySelected) {
      var citiesFiltered = this.cities.filter((city) => {
        return city.name == citySelected;
      });

      if (citiesFiltered.length > 0) {
        return citiesFiltered[0];
      }
    }

    return null;
  }

  loadPickupPoints(isInit: boolean = false) {
    if (this._shippingMethod.city) {
      var filter: PickupPointsFilterModel = {
        city: this._shippingMethod.city,
        countryId: this._shippingMethod.countryId
      }

      this.loadingPickupPoints = true;
      this._shippingService.getPickupPoints(filter).subscribe(data => {
        this.pickupPoints = data;

        if (isInit) {          
          this.selectedPickupPoint = this.findSelectedPickupPoint();
          this.shippingForm.controls[ShippingFormFieldNames.AddressDropdownList].setValue(this.selectedPickupPoint);
          this.shippingFormGroup.emit(this.shippingForm);
        }

        this.loadingPickupPoints = false;
      });
    } else {
      this.loadingPickupPoints = false;
    }
  }

  findSelectedPickupPoint(): DictionaryModel {
    if (this._shippingMethod.pickupPointId == null){
      return null;
    }

    if (this.pickupPoints) {
      var items = this.pickupPoints.filter((item) => {
        return item.id == this._shippingMethod.pickupPointId.toString();
      });

      if (items.length > 0) {
        return items[0];
      }

      return null;
    }
    return null;
  }

  onValueChange(fieldName: string) {    
    var formValues = this.shippingForm.value;

    if (fieldName == ShippingFormFieldNames.FirstNameTextBox) {
      this._shippingMethod.firstName = formValues[ShippingFormFieldNames.FirstNameTextBox];
    }

    if (fieldName == ShippingFormFieldNames.LastNameTextBox) {
      this._shippingMethod.lastName = formValues[ShippingFormFieldNames.LastNameTextBox];
    }

    if (fieldName == ShippingFormFieldNames.PhoneNumberTextBox) {
      this._shippingMethod.phoneNumber = formValues[ShippingFormFieldNames.PhoneNumberTextBox];
    }

    if (fieldName == ShippingFormFieldNames.ZipPostalCodeTextBox) {
      this._shippingMethod.zipPostalCode = formValues[ShippingFormFieldNames.ZipPostalCodeTextBox];
    }

    if (fieldName == ShippingFormFieldNames.CityTextBox) {
      this._shippingMethod.city = formValues[ShippingFormFieldNames.CityTextBox];
    }

    if (fieldName == ShippingFormFieldNames.AddressTextBox) {
      this._shippingMethod.address = formValues[ShippingFormFieldNames.AddressTextBox];
    }
    
    if (fieldName == ShippingFormFieldNames.CityDropdownList) {
      this.selectedCity = formValues[ShippingFormFieldNames.CityDropdownList];

      this._shippingMethod.pickupPointId = null;
      this.selectedPickupPoint = null;
      this._shippingMethod.city = this.selectedCity.name;
      this.loadPickupPoints();
    }
    
    if (fieldName == ShippingFormFieldNames.AddressDropdownList) {
      this._shippingMethod.pickupPointId = formValues[ShippingFormFieldNames.AddressDropdownList].id;
      this.selectedPickupPoint = this.findSelectedPickupPoint();
    }

    var additionalShippingField = this._shippingMethod.additionalShippingFields.find((item) => {
      return item.nameResourceKey == fieldName;
    });
    if (additionalShippingField) {   
      additionalShippingField.value = formValues[additionalShippingField.nameResourceKey];
    }
        
    this.shippingFormGroup.emit(this.shippingForm);
  }

  isHidden(shippingFormFieldName: string) {
    return this._shippingMethod.hiddenShippingFields.indexOf(shippingFormFieldName) > -1;
  }
}
