import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

export class BaseService {
  private readonly _http: HttpClient;
  private readonly _cache: { [key: string]: Observable<any> } = {};

  constructor(http: HttpClient) {
    this._http = http;
  }

  public post<TResponse>(url: string, request?: any, headers?: any): Observable<TResponse> {
    return this._http
      .post<TResponse>(url, request, { headers: headers })
      .pipe(
        map((data: TResponse) => {
          return data;
        }));
  }

  public get<TResponse>(url: string): Observable<TResponse> {
    return this._http
      .get<TResponse>(url)
      .pipe(
        map((data: TResponse) => {
          return data;
        }));
  }

  public postOfCache<TResponse>(url: string, request?: any): Observable<TResponse> {
    if (!this._cache[url]) {
      this._cache[url] = this._http
        .post<TResponse>(url, request)
        .pipe(
          shareReplay(1),
          map((data: TResponse) => {
            return data;
          }));
    }

    return this._cache[url];
  }

  public getOfCache<TResponse>(url: string): Observable<TResponse> {
    if (!this._cache[url]) {
      this._cache[url] = this._http.get<TResponse>(url)
        .pipe(
          shareReplay(1),
          map((data: TResponse) => {
            return data;
          }));

    }
    return this._cache[url];
  }
}
