import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseService } from './base.service';
import { Observable } from 'rxjs';
import { PickupPointsFilterModel } from '../models/generated/PickupPointsFilterModel';
import { CityModel } from '../models/generated/CityModel';
import { ShippingCalculationModel } from '../models/generated/ShippingCalculationModel';
import { ShippingCalculationOptionModel } from '../models/generated/ShippingCalculationOptionModel';
import { ShippingMethodModel } from '../models/generated/ShippingMethodModel';
import { DictionaryModel } from '../models/generated/DictionaryModel';

@Injectable({
  providedIn: 'root'
})
export class ShippingService extends BaseService {
  private endpoint = '/api/shipping';

  constructor(http: HttpClient) { 
    super(http);
  }

  public getShippingMethods(): Observable<ShippingMethodModel[]> {
    return this.get<ShippingMethodModel[]>(`${this.endpoint}/shipping_methods`);
  }

  public getPickupPoints(filter: PickupPointsFilterModel): Observable<DictionaryModel[]> {
    return this.post<DictionaryModel[]>(`${this.endpoint}/pickup_points`, filter);
  }

  public getCitiesOfSelectedShippingComputation(): Observable<CityModel[]> {
    return this.get<CityModel[]>(`${this.endpoint}/cities_of_selected_shipping_computation`);
  }

  public updateInfo(model: ShippingMethodModel): Observable<ShippingMethodModel> {    
    return this.post<ShippingMethodModel>(`${this.endpoint}/update_info`, model);
  }

  public getShippingCalculation(): Observable<ShippingCalculationModel> {
    return this.get<ShippingCalculationModel>(`${this.endpoint}/get_shipping_calculation`);
  }

  public setShippingCalculationOption(model: ShippingCalculationOptionModel): Observable<string> {    
    return this.post(`${this.endpoint}/set_shipping_computation_option`, model);
  }

  public updateDistributorLimits(value: number): Observable<string> {    
    return this.post(`${this.endpoint}/update_distributor_limits`, value);
  }
}
