import { Injectable } from '@angular/core';
import { BaseService } from './base.service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ResourceKeys } from '../constants/generated/ResourceKeys';

@Injectable({
  providedIn: 'root'
})
export class CommonService extends BaseService {
  private _resources: { [key: string]: string; };  

  constructor(private readonly http: HttpClient) {
    super(http);
  }

  public getPluginAllLocaleStringResourcesOfCache(): Observable<{ [key: string]: string; }> {
    return this.getOfCache<{ [key: string]: string; }>('/api/shipping/get_plugin_all_resources').pipe(map(data => {
      this._resources = data;
      return data;
    }));
  }

  public getResource(resourceKey: string): string {
    if (ResourceKeys[resourceKey]) {
      const resourceName = ResourceKeys[resourceKey];
      if (this._resources && this._resources[resourceName]) {
        return this._resources[resourceName];
      } else {
        return resourceName;
      }
    }else{
      return resourceKey;
    }
  }
}
