import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';
import { ShippingService } from 'src/app/services/shipping.service';
import { ShippingCalculationOptionModel } from 'src/app/models/generated/ShippingCalculationOptionModel';

@Component({
  selector: 'app-shipping-country-select',
  templateUrl: './shipping-country-select.component.html',
  styleUrls: ['./shipping-country-select.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class ShippingCountrySelectComponent implements OnInit {
  @Input() contries: ShippingCalculationOptionModel[];

  blockedUI: boolean = false;
  selectedShippingCountry: ShippingCalculationOptionModel;

  constructor(private readonly _shippingService: ShippingService) { }

  ngOnInit() {
    let contriesFiltered = this.contries.filter((item) => {
      return item.isSelected;
    });
    if (contriesFiltered.length > 0) {
      this.selectedShippingCountry = contriesFiltered[0];
    }
  }

  onShippingCountryChange() {
    this.blockedUI = true;
    this._shippingService.setShippingCalculationOption(this.selectedShippingCountry).subscribe(() => {
      this.blockedUI = false;
    });
  }
}

