import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { ShippingCalculationMonthModel } from 'src/app/models/generated/ShippingCalculationMonthModel';
import { ShippingService } from 'src/app/services/shipping.service';

@Component({
  selector: 'app-order-month-select',
  templateUrl: './order-month-select.component.html',
  styleUrls: ['./order-month-select.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class OrderMonthSelectComponent implements OnInit {
  @Input() availableMonths: ShippingCalculationMonthModel[];
  
  selectedOrderMonth: ShippingCalculationMonthModel;
  blockedUI: boolean = false;

  constructor(private readonly _shippingService: ShippingService) { }

  ngOnInit() {
    let availableMonthsFiltered = this.availableMonths.filter((item) => {
      return item.isSelected;
    });
    if (availableMonthsFiltered.length > 0) {
      this.selectedOrderMonth = availableMonthsFiltered[0];
    }
  }

  onOrderMonthChange(){
    this.blockedUI = true;
    this._shippingService.updateDistributorLimits(this.selectedOrderMonth.timestamp).subscribe(() => {
      this.blockedUI = false;
    });
  }
}
